import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
import { ActivatedRoute } from '@angular/router';
import { conexion } from '../../../global'

@Component({
  selector: 'app-almacen',
  templateUrl: './almacen.component.html',
  styleUrls: ['./almacen.component.scss']
})
export class AlmacenComponent implements OnInit {
  descMas: string = "más"
  ameMas: string = "más"
  conexion: string = conexion
  cargado:boolean=false
  almacen=[{
    nombreInstalacion:"",
    direccionInstalacion:'',
    telefonoInstalacion:''
  }]
  miniBodega
  galeria=[
    {
      url:'https://d2knwvu6cegzt1.cloudfront.net/large-compress/15666459306a6e84e69.jpg'
    }
  ]
  unidades

  constructor(private ruta: ActivatedRoute) {
    var id = ruta.snapshot.params.id
    fetch(`${this.conexion}/api/instalaciones/getOne/${id}`)
      .then(response =>{
        return response.json()
      }).then(respuesta=>{
        this.cargado=true
        this.almacen=respuesta.content
        this.miniBodega=respuesta.content.t_miniBodega
        this.galeria=respuesta.content.t_galeria
        this.unidades=respuesta.content.t_unidades
      })
  }

  ngOnInit() {
    var i = 0
    var o = 0
    $('#ver-detalles').click(() => {
      if (i == 0) {
        $('#descripcion').removeClass('collapsed')
        this.descMas = 'menos'
        i++
      } else {
        $('#descripcion').addClass('collapsed')
        this.descMas = 'más'
        i--
      }
    })
    $('#ver-amenidades').click(() => {
      if (o == 0) {
        $('#caracteristicas-amenidades').removeClass('collapsed')
        this.ameMas = 'menos'
        o++
      } else {
        $('#caracteristicas-amenidades').addClass('collapsed')
        this.ameMas = 'más'
        o--
      }
    })
  }

}
